/* eslint-disable */
import ng from 'vendor-bb-angular';

import bbStorageModuleKey, { bbStorageServiceKey } from 'lib-bb-storage-ng';
import limitsData from './data-bb-limits-http';

const limitsDataModuleKey = 'data-bb-limits-http-ng';

export const limitsDataKey = 'data-bb-limits-http-ng:limitsData';

export default ng
  .module(limitsDataModuleKey, [
    bbStorageModuleKey,
  ])
  .provider(limitsDataKey, [() => {
    const config = {
      baseUri: '/',
    };

    return {
      setBaseUri: (baseUri) => {
        config.baseUri = baseUri;
      },
      $get: [
        '$q',
        bbStorageServiceKey,
        '$httpParamSerializer',
        /* into */
       limitsData(config),
      ],
    };
  }])
  .name;



